
.navigation-enter-active {
  transition: all 0.7s cubic-bezier(0.54, 0.01, 0.56, 1);
  opacity: 1;
  top: 0%;
  transition-delay: 0.5s;
}
.navigation-leave-active {
  transition: all 0.7s cubic-bezier(0.54, 0.01, 0.56, 1);
  opacity: 0;
  top: -100%;
  transition-delay: 0.5s;
}
.navigation-enter {
  transition: all 0.7s cubic-bezier(0.54, 0.01, 0.56, 1);
  opacity: 0;
  top: 100%;
}
.navigation-leave {
  opacity: 1;
  transition: all 0.7s cubic-bezier(0.54, 0.01, 0.56, 1);
  top: 0%;
}
.singleComponent-enter-active {
  transition: all 0.7s cubic-bezier(0.54, 0.01, 0.56, 1);
  opacity: 1;
  z-index: 2;
  left: 0%;
}
.singleComponent-leave-active {
  transition: all 0.7s cubic-bezier(0.54, 0.01, 0.56, 1);
  opacity: 1;
  z-index: 1;
  left: -100%;
}
.singleComponent-enter {
  transition: all 0.7s cubic-bezier(0.54, 0.01, 0.56, 1);
  opacity: 1;
  z-index: 2;
  left: 100%;
}
.singleComponent-leave {
  opacity: 1;
  left: -100%;
  z-index: 1;
  transition: all 0.7s cubic-bezier(0.54, 0.01, 0.56, 1);
}
@media screen and (min-width: 1001px) {
