    transition: all 0.7s cubic-bezier(0.54, 0.01, 0.56, 1);
    opacity: 1;
    z-index: 2;
    left: 50%;
}
.singleComponent-leave-active {
    transition: all 0.7s cubic-bezier(0.54, 0.01, 0.56, 1);
    opacity: 1;
    z-index: 1;
    left: -100%;
}
.singleComponent-enter {
    transition: all 0.7s cubic-bezier(0.54, 0.01, 0.56, 1);
    opacity: 1;
    z-index: 2;
    left: 100%;
}
.singleComponent-leave {
    opacity: 1;
    left: 0%;
