
.header {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1;
}
.header .title {
    position: absolute;
    width: 100%;
    top: 38px;
    z-index: 1;
    transition: all 0.5s;
}
.header .title h1 {
      font-family: "Tungsten-Rounded-Medium";
      font-size: 55px;
      letter-spacing: 4px;
      text-align: center;
}
.header .title h2 {
      font-family: "Tungsten-Rounded-Book";
      font-size: 30px;
      letter-spacing: 2px;
      text-align: center;
}
.header .burgerMenu {
    position: absolute;
    z-index: 2;
    left: 60px;
    top: 70px;
    border: solid #000000;
    border-width: 0px;
    list-style: none;
    width: 90px;
    padding: 0;
    height: 40px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    transition: all 0.5s;
}
.header .burgerMenu li {
      width: 28px;
      height: 3px;
      margin-bottom: 5px;
      background-color: #000000;
      transition: all 0.5s;
}
.header .menuContent {
    background-color: #FFFFFF;
    height: 0vh;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    overflow: hidden;
    transition: all 0.5s;
}
.header .menuContent .menuLinks {
      height: 30vh;
      top: 0;
      bottom: 0;
      margin: auto;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: distribute;
          justify-content: space-around;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      font-size: 25px;
      letter-spacing: 7px;
}
.header .menuContent .menuLinks .links {
        font-family: 'GT-America-Regular';
        text-decoration: none;
        color: #000;
        text-transform: uppercase;
        opacity: 1;
        transition: all 0.2s;
}
.header .menuContent .menuLinks .links .fade {
          opacity: 0;
}
.header .menuContent .sharing {
      position: absolute;
      bottom: 120px;
      width: 50%;
      left: 0;
      right: 0;
      margin: auto;
}
.header .menuContent .sharing h3 {
        font-family: "GT-America-Regular";
        font-size: 20px;
        letter-spacing: 5px;
        text-transform: uppercase;
        margin-bottom: 20px;
}
.header .menuContent .sharing .socialLinks {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: distribute;
            justify-content: space-around;
}
.header .menuContent .sharing .facebook, .header .menuContent .sharing .twitter {
        width: 130px;
        height: 130px;
        border: 2px solid #000000;
        background-repeat: no-repeat;
        background-position: 50% 50%;
}
.header .menuContent .sharing .facebook {
        background-image: url("../assets/facebook.png");
        background-size: 30%;
}
.header .menuContent .sharing .twitter {
        background-image: url("../assets/twitter.png");
        background-size: 70%;
}
.header.menu .title {
    top: 200px;
}
.header.menu .burgerMenu {
    border-width: 2px;
}
.header.menu .burgerMenu li {
      margin-bottom: -3px;
}
.header.menu .burgerMenu .first {
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
}
.header.menu .burgerMenu .second {
      -webkit-transform: scaleX(0);
              transform: scaleX(0);
}
.header.menu .burgerMenu .third {
      -webkit-transform: rotate(-45deg);
              transform: rotate(-45deg);
}
.header.menu .menuContent {
    height: 100vh;
}
@media screen and (min-width: 1001px) {
.header .title {
    position: relative;
    width: 20%;
    text-align: left;
    margin-left: 60px;
}
.header .title h1, .header .title h2 {
      text-align: left;
}
.header .title h1 {
      font-size: 25px;
}
.header .title h2 {
      font-size: 15px;
}
.header .burgerMenu {
    display: none;
}
.header .menuContent {
    overflow: visible;
}
.header .menuContent .menuLinks {
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-direction: row;
              flex-direction: row;
      -webkit-box-pack: end;
          -ms-flex-pack: end;
              justify-content: flex-end;
      width: 100%;
}
.header .menuContent .menuLinks .links {
        font-size: 12px;
        margin-right: 60px;
}
.header .menuContent .menuLinks .links:nth-child(1) {
          display: none;
}
}



