.singleComponent {
  height: 100% !important;
  width: 100%;
  position: fixed;
  top: 0;
  background-color: #FFFFFF;
  z-index: 10;
  overflow-x: hidden;
}
.singleComponent .header {
    width: 100%;
    height: 100px;
    background-color: #ffffa1;
    position: fixed;
    z-index: 5;
}
.singleComponent .header .close {
      position: absolute;
      left: 60px;
      top: 35px;
}
.singleComponent article {
    margin: 0px 60px;
    position: relative;
}
.singleComponent article .title {
      background-color: #FFFFFF;
      position: fixed;
      z-index: 2;
      width: 100%;
      height: 230px;
      margin-left: -1px;
}
.singleComponent article .title h2 {
        font-family: 'Tungsten-Rounded-Medium';
        font-size: 55px;
        letter-spacing: 4px;
        text-align: left;
        margin-top: 85px;
}
.singleComponent article .title .categories {
        font-family: 'Tungsten-Rounded-Book';
        font-size: 24px;
        text-align: left;
        margin-top: 4px;
        float: left;
        margin-right: 20px;
}
.singleComponent article .title .progressBar {
        height: 2px;
        width: 25%;
        background-color: #cecece;
        position: absolute;
        bottom: 0;
}
.singleComponent article .title .social {
        width: 10vh;
        position: absolute;
        right: 0;
        z-index: 1;
        top: 50px;
}
.singleComponent article .title .social .facebook, .singleComponent article .title .social .twitter {
          background-repeat: no-repeat;
          width: 70px;
          height: 70px;
          background-position: 50% 50%;
}
.singleComponent article .title .social .facebook {
          background-image: url("../assets/facebook.png");
          background-size: 30%;
}
.singleComponent article .title .social .twitter {
          background-image: url("../assets/twitter.png");
          background-size: 100%;
}
.singleComponent article .postContent {
      margin-top: 80px;
      width: 100%;
      top: 300px;
      position: relative;
      margin-bottom: 400px;
}
.singleComponent article .postContent img {
        width: 100%;
}
.singleComponent article .postContent .postText {
        margin: 50px 60px;
        font-family: "GT-America-Light";
        font-size: 25px;
        line-height: 44px;
        letter-spacing: 0.2px;
        text-align: justify;
        color: #605359;
}
.singleComponent .bottom {
    height: 100px;
    width: 100%;
    background-color: #ffffa1;
    position: fixed;
    bottom: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.singleComponent .bottom a {
      font-family: GT-America-Regular;
      text-decoration: none;
      color: #000;
      font-size: 20px;
      letter-spacing: 2.9px;
      text-transform: uppercase;
}
@media screen and (min-width: 1001px) {
.singleComponent {
    width: 50%;
    right: 0;
    border-left: solid 1px #979797;
}
.singleComponent .header {
      background-color: transparent;
}
.singleComponent article .title {
      width: 100%;
      top: 0;
}
.singleComponent article .title h2 {
        font-size: 25px;
        margin-top: 120px;
}
.singleComponent article .title .categories {
        font-size: 16px;
}
.singleComponent article .postContent .postText {
      font-size: 18px;
      line-height: 29px;
}
.singleComponent .bottom {
      background-color: transparent;
      width: 50%;
      -webkit-box-pack: end;
          -ms-flex-pack: end;
              justify-content: flex-end;
}
.singleComponent .bottom .bottomLinks {
        width: 50vh;
        left: 50vh;
}
.singleComponent .bottom .bottomLinks:nth-child(1) {
        display: none;
}
.singleComponent .bottom .bottomLinks {
        width: 300px;
        height: 60px;
        background-color: #ffffa1;
        font-size: 12px;
        margin-right: 60px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
}
}



